
  .title {
    user-select: none;
    text-transform: capitalize;
    font-size: 16px;
    opacity: 0.75;
    position: relative;
    display: inline-block;
    margin: 15px 0px 20px 0;

    margin-top: 32px;
opacity: 0.75;
font-size: 12px;

    &:after {
      content: "";
      height: 5px;
      width: 1000px;
      left: calc(100% + 20px);
      top: 50%;
      position: absolute;
      background-color: rgba(255, 255, 255, 0.25);
    }
  }
