@font-face {
  font-family: MCFFR;
  src: url(../fonts/mcffr.woff) format("woff");
}

@font-face {
  font-family: pixy;
  src: url(../fonts/pixy.woff) format("woff");
}

* {
  margin: 0;
  padding: 0;
  font-family: pixy;
  font-weight: lighter;
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.125) transparent;
}

main {
  background-color: #143055;
  height: calc(100vh - 80px - 7px);
  z-index: 1;
  position: relative;
  overflow-y: auto;
}

p, h1, h2, h3 {
  letter-spacing: -0.01px;
  transform: translate3d(0,0,0);
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.125);
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.25);
}

.none {
  display: none;
}

.img_container {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

img.pixelated {
  image-rendering: pixelated;
}
.pixelated img {
  image-rendering: pixelated;
}
