#aside_search {
  #search_list {
    height: calc(100% - 125px);
    overflow: auto;
    list-style: none;

    li {
      font-size: 16px;
      padding: 10px;
      margin: 20px 0;
      width: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-color: rgba(255, 255, 255, 0.125);
      position: relative;

      img {
        // position: absolute;
        // top: 20px;
        // right: 20px;
        height: 22px;
        width: 22px;
        transform: translateY(5px);
        margin-left: 7px;
        image-rendering: pixelated;
      }

      &:hover {
        background-color: rgba(255, 255, 255, 0.25);
        filter: brightness(1.2);
      }
    }
  }

  #search {
      width: calc(100% - 18px);
      margin-bottom: 20px;
    }

    ul .hidor {
      padding: 0;
    }
}
