.jjournal {
  user-select: text;
  position: relative;
  image-rendering: pixelated;
  padding: 20px;
  color: rgba(0, 0, 0, 0.66);
  line-height: 20px;
  background-image: url("https://media.discordapp.net/attachments/1003060853253357658/1005510136720265278/book-content.png");
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 16px;
  min-height: 50px;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 68px;
    width: 100%;
    background-image: url("https://media.discordapp.net/attachments/1003060853253357658/1005515260251340861/book-top.png");
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
  }

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 34px;
    width: 100%;
    background-image: url("https://media.discordapp.net/attachments/1003060853253357658/1005510137106153672/book-bottom.png");
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 2;
  }

  > p {
    z-index: 3;
    position: relative;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: rgba(0, 0, 0, 0.25) transparent;
    max-height: 200px;
    padding: 2px 5px 5px 20px;
  }

  .brand {
    background-image: url("https://cdn.discordapp.com/attachments/1003060853253357658/1005516359368716420/book-c.png");
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 3;
    position: absolute;
    top: 0;
    left: 13px;
    height: 62px;
    width: 14px;
    filter: drop-shadow(4px 5px 0px rgba(0, 0, 0, 0.25));
  }

  // &:nth-child(8) {
  // }

}

.journal {
  position: relative;
  image-rendering: pixelated;
  padding: 20px;
  color: rgba(0, 0, 0, 0.66);
  line-height: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 16px;
  min-height: 50px;
  background-color: #f1e6c6;

  > *:not(.journal_img) {
    z-index: 3;
    position: relative;
  }

  .journal_img {
    > div {
      position: absolute;
      width: 50px;
      height: 50px;
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 1;

      &:nth-child(1) {
        top: 0;
        left: 0;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160381359194214/j-top-left.png);
        z-index: 2;
      }

      &:nth-child(2) {
        top: 0;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160381065605291/j-top-right.png);
        right: 0;
        z-index: 2;
      }

      &:nth-child(3) {
        bottom: 0;
        right: 0;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160380444848179/j-bottom-right.png);
        z-index: 2;
      }

      &:nth-child(4) {
        left: 0;
        bottom: 0;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160380809744457/j-bottom-left.png);
        z-index: 2;
      }

      &:nth-child(5) {
        top: 0;
        left: 0;
        width: 100%;
        background-repeat: repeat;
        background-size: contain;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160381719916584/j-top.png);
      }

      &:nth-child(6) {
        top: 0;
        right: 0;
        height: 100%;
        background-repeat: repeat;
        background-size: contain;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160382353252463/j-right.png);
      }

      &:nth-child(7) {
        bottom: 0;
        left: 0;
        width: 100%;
        background-repeat: repeat;
        background-size: contain;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160382571364453/j-bottom.png);
      }

      &:nth-child(8) {
        top: 0;
        left: 0;
        height: 100%;
        background-repeat: repeat;
        background-size: contain;
        background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1017160382038691920/j-left.png);
      }
    }
  }


    table {
      position: relative;
      z-index: 3;
      font-size: 12px;
      width: 100%;

      tr {
        // height: 30px;
        margin: 5px 0;

        td {
          padding: 10px 0 10px 10px;

          p {
            width: 75%;

            img {
              height: 22px;
              width: 22px;
              transform: translateY(5px) scale(1);
              margin-left: 2px;
              image-rendering: pixelated;
            }
          }
        }

        td:first-child {
          background-color: #dccba9;

          font-size: 12px;
        }

        td:last-child {
          p {
            border-bottom: 4px solid #dccba9;
            padding-bottom: 5px;
          }
        }
      }
    }

    ul.grid {
      z-index: 3;
      position: relative;
      display: flex;
      flex-wrap: wrap;
      list-style: none;
        justify-content: left;

      li {
        background-color: #dccba9;
        margin: 5px;
        display: flex;
        flex-direction: column;
        text-align: center;
        position: relative;
        width: calc(33% - 10px);

        p {
          font-size: 13px;
          width: 100%;
          margin: 4px 0;

          span {
            height: 50%;
          }
          // white-space: nowrap;
        }

        img {
          width: 45px;
          margin-top: 4px;
          margin-right: auto;
          margin-left: auto;
        }
      }
    }

      .gallery {
        max-height: 200px;
        overflow-y: scroll;
        z-index: 3;
        position: relative;

        img {
          width: calc(50% - 20px);
          margin: 10px;
          margin-bottom: 5px;

          &:nth-child(2n + 1) {
            margin-right: 5px;
          }
        }
      }
}
