#aside_profile {
  .hidor {
    padding: 0;
  }

  #quote {
    margin: 20px 0 5px 0;
    font-size: 12px;
    user-select: text;

    &:before {
      content: '" ';
    }
    &:after {
      content: ' "';
    }
  }
  h1 {
    position: absolute;
    margin: 0 0 20px 25px;
    filter: drop-shadow(0 0 10px rgba(0, 0, 0, 1));
    font-weight: 600;
    bottom: 0;
    font-size: 24px;
    white-space: nowrap;
    width: calc(100% - 50px);
    overflow: hidden;
    text-overflow: ellipsis;
    user-select: text;
    z-index: 10;
  }

  > div:nth-child(2) {
    overflow-y: auto;
    width: calc(100%);
    overflow-y: auto;
    height: calc(100% - 180px);

    > div > p {
      margin-top: 32px;
      opacity: 0.75;
      font-size: 12px;
    }
  }

  > div:nth-child(1) {
    &.border {
      div {
        --light: 0.2;
      }
      > div {
        box-shadow: none !important;
      }
    }
    overflow: hidden;
    &::after {
      content: "";
      position: absolute;
      height: 10px;
      width: 100%;
      left: 0;
      top: -10px;
      box-shadow: 0 5px 75px 20px rgba(0, 0, 0, 0.75);
      z-index: 95;
    }

    #profile_img {
      // margin-top: 5px;
      margin: 5px;
      width: calc(100% - 10px);
      height: 150px;
      background-position: center;
      background-size: cover;
      position: relative;
      filter: contrast(115%);
    }
  }

  .journal > p {
    position: relative;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: rgba(0, 0, 0, 0.25) transparent;
    max-height: 200px;
    padding: 2px 5px 5px 20px;
    user-select: text;
  }
}
