.item_panel {
  width: 200px;
  margin: 15px;
  cursor: pointer;

  .img_container {
    // border: 5px solid rgba(0,0,0,.25);
    margin: 5px;
    margin-bottom: 0;
    background-color: rgba(0, 0, 0, 0.2);

    img {
      --size: 112px;
      width: var(--size);
      height: var(--size);
      margin: 20px auto;
      filter: drop-shadow(0 0 30px rgba(255, 255, 255, 0.1)) contrast(115%);
    }
  }

  .journal {
    ul {
      list-style: none;
    }
  }

  .border {
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;

    .border_width > div {
      --light: 0.1;
      --dark: 0.3;
    }
  }

  p {
  }
}
