#aside_custom {
  display: flex;
  flex-direction: column;
  .hidor p:first-child {
    margin-top: 0;
  }
  .select {
    height: 50px;
    width: 100%;

    select {
      width: 100%;
      appearance: none;
    }

    option {
    }
  }

  button {
    font-size: 12px;
  }

  .custom_input {
    margin: 10px 0;
    input {
      margin: 2px 0;
    }
  }

  #custom_container {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      background-color: #cbcbcb;
      margin-bottom: 15px;

      .border {
        pointer-events: none;

        .border {
          position: absolute;
          right: 15px;
          bottom: 15px;
          div {
          --light: .2;}
          button {
            font-size: 12px;
            background-color: #b54157;
            color: white;
            padding: 5px 10px 7px 16px;
            pointer-events: all;
          }
        }
      }

      p {
        padding: 10px;
        max-width: 250px;
        text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
      }


    }
  }

  > div:nth-child(2) {
    overflow-y: auto;
    padding: 0;
  }

  div:nth-child(2) > div:nth-child(1) {
    padding: 0;
  }
}
