#aside_settings {

  table {
    width: 100%;
  }

  td {
    width: 50%;
    font-size: 14px;
    line-height: 25px;
    padding-bottom: 25px;
    padding-right: 20px;
  }

  td:nth-child(2) {
    width: 50%;
  }

  button {
    width: 100%;
    height: 50px;
    color: white;
    font-family: MCFFR;
    padding: 5px;

    &.active {
      background-color: #70b35b;
    }

    > div {
      // pointer-events: none;
    }
  }

  .hidor > div {
    justify-content: space-between;
    align-items: center;
  }

}
