input[type="text"],
textarea {
  width: calc(100% - 10px);
  background-color: rgba(0, 0, 0, 0.25);
  color: white;
  border: none;
  font-size: 16px;
  padding: 10px 0 10px 10px;
  resize: none;

  &:focus,
  &:active {
    background-color: rgba(255, 255, 255, 0.25);
    outline: none;
  }
}
