.border {
  position: relative;

  .hover:hover {
    scale: 1.5;
  }

  .border_width {
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3),
    div:nth-child(4) {
      position: absolute;
      --light: 0.5;
      --dark: 0.15;
      --width: 5px;
      z-index: 2;

      &:nth-child(1) {
        height: var(--width);
        width: 100%;
        background-color: rgba(255, 255, 255, var(--light));
        top: 0;
        left: 0;
        // box-shadow: 0 0 20px 3px rgba(255, 255, 255, 0.33);

        &.inverted {
          background-color: rgba(0, 0, 0, var(--dark));
        }
      }

      &:nth-child(2) {
        width: var(--width);
        height: 100%;
        background-color: rgba(0, 0, 0, var(--dark));
        top: 0;
        left: calc(100% - var(--width));

                &.inverted {
                  background-color: rgba(255, 255, 255, var(--light));
                }
      }

      &:nth-child(3) {
        width: var(--width);
        top: 0;
        left: 0;
        height: 100%;
        background-color: rgba(255, 255, 255, var(--light));
        // box-shadow: 0 0 20px 3px rgba(255, 255, 255, 0.33);
                &.inverted {
                  background-color: rgba(0, 0, 0, var(--dark));
                }
      }

      &:nth-child(4) {
        height: var(--width);
        width: 100%;
        background-color: rgba(0, 0, 0, var(--dark));
        top: calc(100% - var(--width));
        left: 0;
                &.inverted {
                  background-color: rgba(255, 255, 255, var(--light));
                }
      }
    }
  }
}

.active.border div,
.active .border div {
  --light: 0.7;
}
