.select {
  display: block;
  background-color: transparent;
  position: relative;
  cursor: pointer;
  height: 50px;
  cursor: pointer;
  user-select: none;

  label,
  ul li {
    height: 100%;
    width: 100%;
    display: flex;
    padding: 10px;
    align-items: center;
    background-color: #cbcbcb;
    width: calc(100% - 20px);

    &:hover {
      filter: brightness(1.1);
    }
  }

  label {
    padding: 0 10px;

    img {
      height: 69%;
      margin-right: 10px;
    }

    &:before {
      position: absolute;
      content: "";
      height: 50px;
      width: 50px;
      background-image: url(https://cdn.discordapp.com/attachments/1003060853253357658/1010991661888843847/arrowr.png);
      background-size: 22px 22px;
      background-repeat: no-repeat;
      background-position: center;
      right: 0px;
      transform: rotate(90deg);
      image-rendering: pixelated;
      pointer-events: none;
    }
  }

  ul {
    position: relative;
    display: none;
    z-index: 10;
    list-style: none;
    margin: 10px 0;
    background-color: #cbcbcb;
    box-shadow: 5px 5px 0 0 rgba(0, 0, 0, 0.25);

    li {
      img {
        --size: 64px;
        width: var(--size);
        height: var(--size);
      }
    }
  }

  &.open {
    ul {
      display: block;
    }

    ul.inline div div {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      li {
        width: auto;
      }
    }

    label:before {
      transform: rotate(-90deg);
    }
  }
}
